body {
  margin-top: 90px; /* Ajusta la altura según sea necesario para evitar que el navbar tape el contenido */
}
/* -------------NAVBAR----------------------- */
.navbar-container {
  background-color: #ccc; /* Fondo gris para el contenedor */
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px; /* Altura ajustada para la barra de navegación */
}

.bg-body-tertiary {
  background-color: #a8a8a8 !important; /* Cambia el color de fondo de la barra de navegación a azul */
  padding: 18px 0;
  position: fixed;
  width: 100%;
   top: 0;
}
.navbar {
  background: linear-gradient(to right, #36afb8, #b34898); /* Cambia los colores según tus preferencias */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.navbar-nav {
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar-nav .nav-link {
  margin-right: 15px; /* Espaciado entre enlaces de navegación */
}

.navbar-toggler {
  margin-right: 0; /* Ajuste del margen para el botón de alternancia */
}

.navbar-nav a {
  font-size: 20px; /* Ajusta el tamaño de la fuente según tus necesidades */
  transition: color 0.3s; /* Agrega una transición para suavizar el cambio de color */
}

.navbar-nav .nav-link:hover,
.navbar-nav .nav-item:hover {
color: #ffffff; /* Cambia el color del texto al pasar el cursor sobre el enlace */
transform: translateY(-3px); /* Eleva el texto 3 pixels al pasar el cursor */
background: linear-gradient(to right, #191796, #00eeff);
-webkit-background-clip: text; /* Aplica el degradado solo al texto */
background-clip: text; /* Aplica el degradado solo al texto */
color: transparent; /* Hace que el texto sea transparente para que se vea el degradado */
}

.dropdown-menu a {
  transition: color 0.3s, transform 0.3s; /* Agrega transición para el cambio de color y elevación */
}

/* Cambia el color de las letras y aplica una elevación al pasar el cursor sobre los enlaces del menú desplegable */
.dropdown-menu a:hover {
  background: linear-gradient(to right, #191796, #00eeff);
  -webkit-background-clip: text; /* Aplica el degradado solo al texto */
  background-clip: text; /* Aplica el degradado solo al texto */
  color: transparent; /* Hace que el texto sea transparente para que se vea el degradado */
}

.logo-img {
  width: 180px; /* Ajusta el ancho según tus necesidades */
  height: 40px; /* Ajusta la altura según tus necesidades */
}
/* ----------head-------- */
