/* ------------------imgane carousel inicio------------- */
/* Estilos para las imágenes del carousel */
.carousel-inner img {
  width: 100%; /* Ajusta el ancho de las imágenes al 100% del contenedor */
  height: 300px; /* Mantiene la proporción de aspecto original de las imágenes */
}

/* Estilos adicionales para ajustar el tamaño del carousel */
.carousel {
  max-width: 800px; /* Ajusta el ancho máximo del carousel según sea necesario */
  margin: 0 auto; /* Centra el carousel en el contenedor principal */
}

/* -------------estilo video inicio------------ */
.player-wrapper{
  position: relative;
  padding-top: 56.25%;
  overflow: hidden;
  max-width: 100%;
  border-radius: 15px
}
.react-player{
  position:absolute;
  top: 0;
  left: 0;
}
