/* ------------css alphabet----------- */
.above-columns {
  text-align: center; /* Ajusta según sea necesario */
  padding: 20px; /* Agrega espacio alrededor del contenido si es necesario */
  background-color: #e0e0e0; /* Cambia el color de fondo según sea necesario */
}

.above-columns h1 {
  font-family: 'TuTipoDeLetra', sans-serif; /* Reemplaza 'TuTipoDeLetra' con el nombre de tu fuente deseada */
}

/* Agrega estilos a tu reproductor de vídeo */

.alphabet-img {
  width: 100%; /* Adjust the width as needed */
  height: 200px; /* Maintain the aspect ratio */
}

/* Optionally, you can add some margin to the images */
.alphabet-img + .alphabet-img {
  margin-top: 5px; /* Adjust the margin as needed */
}

.container.text-center {
  display: flex;
  justify-content: center;
  align-items: center;
}
/* ----------------facebook---------- */
.mark {
  width: 100%; /* Adjust the width as needed */
  height: auto; /* Maintain the aspect ratio */
  border-radius: 8px; /* Add rounded corners if desired */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Add a subtle shadow effect */
  margin-bottom: 20px; /* Adjust the margin as needed */
  margin-top: 20px;
}

.reacciones {
  width: 100%; /* Adjust the width as needed */
  height: auto; /* Maintain the aspect ratio */
  border-radius: 8px; /* Add rounded corners if desired */
  margin-top: 10px;
}
.mesenger {
  display: block; /* Ensure the image is treated as a block element */
  margin: 0 auto; /* Center the image horizontally */
  /* Add any additional styling for the Messenger image if needed */
  max-width: 100%; /* Ensure the image doesn't exceed its container width */
  height: 150px;    /* Maintain the aspect ratio */
  width: 60%;
  margin-top: 20px;
  margin-bottom: 20px;
}
/* --------------formulario------------- */
.above-columns1 {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  background-color: #f9f9f9;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.formula {
  background-color: #ffffff; /* Cambia el color de fondo según tus preferencias */
}



input {
  width: 60%;
  margin-bottom: 15px;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
}

button {
  width: 40%;
  background: linear-gradient(to right, #191796, #00eeff); /* Cambia los colores según tus preferencias */
  color: white;
  padding: 12px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.2s ease;
}

button:hover {
  background: linear-gradient(to right, #191796, #00eeff); /* Cambia los colores según tus preferencias */
  transform: translateY(-3px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* -----------------ceditos----------------- */
.creditos {
  background-color: #b2c2d386; /* Puedes cambiar el color según tus preferencias */
  color: rgb(0, 0, 0); /* Color del texto */
